import Vue from 'vue'
import App from './App.vue'

import './css/normalize.css'    //css
import './css/base.css'    //css
import './css/globa.css'    //css

import router from './router'  //
Vue.use(router)

import Mint from 'mint-ui'     //mint-ui
import 'mint-ui/lib/style.css'
// import mui from "./lib/mui/js/mui.min.js"
Vue.use(Mint)

import VueResource from 'vue-resource'   //
Vue.use(VueResource)

import './lib/mui/css/mui.css'   //mui
import './lib/mui/css/icons-extra.css'  //mui

import VuePreview from 'vue-preview'   //
Vue.use(VuePreview)

import NavBar from "./components/common/navBar"
import common from './components/common/com-mon'
import counter from './components/common/counter'

import Vuex from 'vuex'   //vuex
Vue.use(Vuex)

Vue.component('navBar', NavBar)
Vue.component('common', common)
Vue.component('counter', counter)

import axios from 'axios'   //axios
Vue.prototype.$ajax = axios   //ajax   
axios.defaults.baseURL = 'http://182.254.146.100:8899/api/'
import qs from 'qs'
axios.interceptors.request.use((req) => {
  if (req.method === 'post') {
    req.data = qs.stringify(req.data);
  }
  return req;
}, (error) => Promise.reject(error));
