<template>
    <div class="com-contain">
      <h3></h3>
      <textarea name="" v-model="msg" id="" cols="10" rows="5" placeholder=""></textarea>
      <mt-button type="primary" size="large" @click="fabiao"></mt-button>
      <div class="cmt-list" >
        <div class="cmt-title">
          1  2015-1-1
        </div>
        <div class="cmt-body">
          
        </div>
        <div class="cmt-title">
          1  2015-1-1
        </div>
        <div class="cmt-body">
          
        </div>
      </div>
      <div class="cmt-list" v-for="(item,i) in info" :key="item.add_time">
        <div class="cmt-title">
          {{i+1}} {{item.user_name}} {{item.add_time |dateFormat}}
        </div>
        <div class="cmt-body">
          {{item.content==='undefined' ? " " :item.content}}
        </div>
      </div>
      <mt-button type="danger" size="large" @click="getmore"></mt-button>
    </div>
</template>

<script>
  import MtButton from "mint-ui/packages/button/src/button";
  export default {
    name: "com-mon",
    components: {MtButton},
    props:['ppid'],
    data(){
      return {
        msg:"",
        pageindex:1,
        info:[]
      }
    },
    created(){
      this.$ajax.get('xxx/'+this.ppid+'?pageindex'+this.pageindex)
      .then(res=>{
        // 
        this.info=this.info.concat(res.data.message)
      })
      .catch(err=>{
        console.log("+id")
      })
    },
    methods:{
      getmore(){
        this.pageindex++
        this.$ajax.get('xxx/'+this.ppid+'?pageindex'+this.pageindex)
          .then(res=>{
          this.info=this.info.concat(res.data.message)
      })
      .catch(err=>{
        console.log("+id")
        })
      },
      fabiao(){
        // postpost    emulateJSON:true
        this.$ajax.post('xxx/'+this.ppid+'/pageindex'+this.pageindex,{content:this.msg.trim()})
        .then(res=>{
          // var com={id:1,name:'xx',add_time:data()}
          // this.info=res.data.message.unshift(com)
          var cmt={user_name:"",add_time:Date.now(),content:this.msg.trim()}
          this.info.unshift(cmt)
          this.msg=''
        }).catch(err=>{

        })
      }
    }
  }
</script>

<style scoped>
.com-contain h3{
  font-size: 16px;
}
.com-contain textarea{
  font-size: 14px;
  margin: 0;
}
.com-contain .cmt-list{
  margin: 15px 0;
}
.com-contain .cmt-list .cmt-title{
  line-height: 30px;
}
.com-contain .cmt-list .cmt-body{
  line-height: 30px;
  text-align: left;
  text-indent: 1em;

}
</style>



