<template>
    <div class="tem1">
      <!--<navBar title=""></navBar>-->
      <!--<nav-bar title=""></nav-bar>-->
      <!--<nav-bar title=""></nav-bar>-->
      <div>
        <h3>{{newsinfo.title}}</h3>
        <div class="con-con">
          <span>:{{newsinfo.add_time | dateFormat}}</span>
          <span class="dianjicishu">{{newsinfo.click}}</span>
        </div>
        <div class=news-content v-html="newsinfo.content"></div>
      </div>
      <div>
        <h3></h3>
        <div class="con-con">
          <span></span>
          <span></span>
          <span class="dianjicishu"></span>
        </div>
        <div class=news-content>+++++2003
          
          
          
          2003
          
          200198%
          20043.1%12.619.56
        </div>
      </div>
      <common :ppid="this.id"></common>
    </div>
</template>

<script>
  export default {
    name: "newsinfo",
    data(){
      return {
        newsinfo:{},  //
        // id:this.$route.params.id   //id
        id:1
      }
    },
    created(){
      //   url   
      // var id = this.$route.params.id
      this.$ajax.get('getnew/'+this.id).then(res =>{
        // 
        this.newsinfo=this.res.data.message[0]
      }).catch(err =>{
        console.log("")
      })
    }
  }
</script>

<style scoped>
  .tem1 h3{
    text-align: center;
    color: red;
  }
  .tem1 .con-con{
    height: 30px;
    border-bottom: 1px solid red;
    margin-left: 15px;
    margin-right: 15px;
  }
  .tem1 .con-con .dianjicishu{
    position: relative;
    display: inline-block;
    margin-right: 15px;
  }
.tem1 div:nth-child(1){
  display: flex;
  justify-content:left;
}
</style>



